import Vue    from 'vue'
import Router from 'vue-router'
import Home   from '@/components/Home'

Vue.use(Router)

export default new Router({
  base: process.env.BASE_URL,
  routes: [{
    path: '/',
    component: Home
  }, {
    path: '/papers',
    component: lazyLoad('Papers'),
  }]
})


function lazyLoad(x) { return () => import(/* webpackChunkName: "view-[request]" */ `@/components/${ x }`) }
