<template>
<v-navigation-drawer v-model='drawer' app fixed clipped color='primary' disable-resize-watcher>
  <v-list class='pt-0' color='primary'>
    <v-list-tile v-for='(x, i) in menu' :key='i' ripple :to='x.to'>
      <v-list-tile-action>
        <v-icon>{{ x.icon }}</v-icon>
      </v-list-tile-action>
    
      <v-list-tile-content>
        <v-list-tile-title>{{ x.name }}</v-list-tile-title>
      </v-list-tile-content>
    </v-list-tile>
  </v-list>
</v-navigation-drawer>
</template>


<script>
import { mapState } from 'vuex'

export default {
  data: () => ({
    drawer: false,
  }),
  computed: {
    ...mapState(['menu'])
  },
  mounted() {
    this.$root.$on('onDrawer', () => this.drawer = !this.drawer)
  }
}
</script>


