<template>
<v-app>
  <AppToolbar v-show='!isHome' />

  <AppDrawer class='hidden-sm-and-up' />
  
  <VuePerfectScrollbar class='scroll-area' :settings='settings'>
    <v-content class='pa-0 ma-0'>
      <v-layout justify-center class='white' :style='styleMTop'>
        <v-flex xs12 :md11='!isHome' :lg10='!isHome' :xl9='!isHome'>
          <v-slide-y-reverse-transition mode='out-in'>
            <router-view />
          </v-slide-y-reverse-transition>
        </v-flex>
      </v-layout>
    </v-content>
    
    <AppFooter v-show='!isHome' />
  </VuePerfectScrollbar>
</v-app>
</template>


<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'

import AppToolbar from '@/components/shared/Toolbar'
import AppDrawer  from '@/components/shared/Drawer'
import AppFooter  from '@/components/shared/Footer'


export default {
  data: () => ({
    settings: {
      minScrollbarLength: 200,
      maxScrollbarLength: 400,
      suppressScrollX   : true,
    },
    isHome: true
  }),
  computed: {
    styleMTop() {
      return {
        'padding-top': !this.isHome ? '48px' : ''
      } 
    },
  },
  methods: {
    onFullScreen(i = true) {
      if(i) {
        const e = document.documentElement
        if     (e.requestFullscreen)       e.requestFullscreen()
        else if(e.mozRequestFullScreen)    e.mozRequestFullScreen()
        else if(e.webkitRequestFullscreen) e.webkitRequestFullscreen()
        else if(e.msRequestFullscreen)     e.msRequestFullscreen()
      } else {
        if(document.exitFullscreen)            document.exitFullscreen()
        else if(document.mozCancelFullScreen)  document.mozCancelFullScreen()
        else if(document.webkitExitFullscreen) document.webkitExitFullscreen()
        else if(document.msExitFullscreen)     document.msExitFullscreen()        
      }
    },
    setTheme() {
      this.$vuetify.theme.primary = _.sample(['#3D9BD4', '#41C0E6', '#41CACB', '#52C497', '#39A246', '#E9B411', '#F46589', '#D557A5', '#DD85CA', '#B779CC', '#393582', '#00438A'])      
    }
  },
  created() {
    this.setTheme()
  },
  beforeMount() {
    this.onFullScreen()
  },
  watch: {
    '$route'({ path }) {
      return path === '/' ? _.delay(() => {
        this.onFullScreen(path === '/')
        this.isHome = true
        this.setTheme()
      }, 250) 
      : _.delay(() => {
        this.onFullScreen(path === '/')
        this.isHome = false
      }, 250)      
    }
  },
  components: {
    VuePerfectScrollbar,
    AppToolbar,
    AppDrawer,
    AppFooter
  }
}
</script>


<style lang='stylus' scoped>
.scroll-area
  position: relative
  width   : 100%
  height  : 100%
  overflow: scroll
  -webkit-overflow-scrolling: touch
</style>
